/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1.1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --corpRed: hsl(4, 77%, 54%);
  --corpRed80: hsl(4, 77%, 54%, 0.8);
  --lightGrey: hsl(0, 0%, 83%);
  --midGrey: hsl(0, 0%, 41%);
  --darkGrey: hsl(0, 0%, 25%);
  --black: hsl(0, 0%, 9%);
  --white: hsl(0, 0%, 100%);
  --white80: hsla(0, 0%, 100%, 0.8);
  --textGrey: hsl(0, 0%, 44%);
  --linkHover: hsl(210, 80%, 49%);
}

:root {
  --font-text: "Lato", Helvetica, Arial, sans-serif; /* body font */
  --font-heading: "Roboto", Helvetica, Arial, sans-serif; /* heading font */
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-text);
  background-color: var(--white);
}

.container-lg {
  width: 80%;
  max-width: 87.5rem;
  margin: 0 auto;
}
@media (max-width: 63.9375em) {
  .container-lg {
    width: 100%;
    position: relative;
  }
}

.container {
  width: 80%;
  max-width: 75rem;
  margin: 0 auto;
}
@media (max-width: 63.9375em) {
  .container {
    width: 100%;
    position: relative;
    padding: 0 0.625rem 0 0.625rem;
  }
}

.row {
  padding: 0 0.625rem;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

img {
  max-width: 100%;
}

a,
a:visited,
a:hover {
  text-decoration: none;
}

.flex {
  display: flex;
}
.flex-jc-sb {
  justify-content: space-between;
}
.flex-jc-c {
  justify-content: center;
}
.flex-ai-c {
  align-items: center;
}

@media (max-width: 63.9375em) {
  .hide-for-mobile {
    display: none;
  }
}

@media (min-width: 75em) {
  .hide-for-desktop {
    display: none;
  }
}

/* Full Width
/* -------------------------------------*/
.fullWidth__blogs--wrapper {
  width: 60%;
  max-width: 75rem;
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
@media (max-width: 63.9375em) {
  .fullWidth__blogs--wrapper {
    width: 100%;
    position: relative;
    flex-direction: row;
  }
}
@media (max-width: 39.9375em) {
  .fullWidth__blogs--wrapper {
    width: 100%;
    position: relative;
    flex-direction: column;
  }
}
.fullWidth__rowReverse {
  flex-direction: row-reverse;
}
.fullWidth__section {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
  overflow: hidden;
  position: relative;
  justify-content: flex-start;
  align-items: center;
}
.fullWidth__section--reverse {
  flex-direction: row-reverse;
}
@media (max-width: 39.9375em) {
  .fullWidth__section {
    display: block;
    height: 100%;
    position: relative;
    overflow: visible;
  }
}

.section__divider {
  margin-bottom: 5rem;
}

.contact__section {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 5rem;
  flex-direction: row;
}
.contact__section--content {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  gap: 2.5rem;
  width: 50%;
}
@media (max-width: 39.9375em) {
  .contact__section--content {
    flex-direction: column;
  }
}
.contact__section--text {
  width: 60%;
}
.contact__section--text p {
  font-family: var(--font-text);
  font-size: 1.375rem;
  font-weight: 700;
  color: var(--textGrey);
}
.contact__us--img {
  border-radius: 50%;
  border: 0.3125rem solid var(--midGrey);
}

.btn__contact a {
  padding: 0.5rem 1.25rem;
  background-color: var(--white);
  border: 0.0625rem solid var(--darkGrey);
  color: var(--darkGrey);
  border-radius: 0;
  margin-left: 1.875rem;
  cursor: pointer;
}
@media (max-width: 63.9375em) {
  .btn__contact a {
    margin-left: 0;
  }
}
.btn__contact a:hover {
  background-color: var(--corpRed);
  border: 0.0625rem solid var(--darkGrey);
  color: var(--white);
  transition: color 350ms ease-in-out;
}

h1,
h2,
h3,
h4 {
  margin-top: 0;
  font-family: var(--font-heading);
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.header {
  background-color: var(--white80);
  position: absolute;
  left: 0;
  right: 0;
  margin: 0 auto;
  width: 100%;
  box-shadow: 0 0 0.625rem var(--black);
  z-index: 999;
}
@media (min-width: 75em) {
  .header .hamburger {
    visibility: hidden;
  }
}
.header.open .header__toggle > span:first-child {
  transform: rotate(45deg);
}
.header.open .header__toggle > span:nth-child(2) {
  opacity: 0;
}
.header.open .header__toggle > span:last-child {
  transform: rotate(-45deg);
}
.header__logo {
  height: 5.1875rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (max-width: 39.9375em) {
  .header__logo img {
    width: 100%;
  }
}
.header__toggle {
  margin-right: 1.25rem;
}
.header__toggle > span {
  display: block;
  width: 1.625rem;
  height: 0.125rem;
  background-color: var(--corpRed);
  transition: all 300ms ease-in-out;
  transform-origin: 0.1875rem 0.0625rem;
}
.header__toggle > span:not(:last-child) {
  margin-bottom: 0.3125rem;
}

.nav {
  height: auto;
  position: relative;
}
.nav__link {
  color: var(--darkGrey);
  text-transform: uppercase;
  text-decoration: none;
  font-family: var(--font-heading);
  font-size: 1.0625rem;
  font-weight: 500;
  letter-spacing: 0.0625rem;
}
@media (max-width: 63.9375em) {
  .nav__link {
    margin-top: 1.25rem;
  }
}
.nav__link:hover, .nav__link:active {
  color: var(--corpRed);
}
@media (max-width: 39.9375em) {
  .nav__link {
    margin-top: 1.25rem;
  }
}
.nav__item {
  margin: 0 0 0 1.5rem;
}
@media (max-width: 63.9375em) {
  .nav__item {
    padding: 1.25rem 0;
    margin: 0;
  }
}
.nav__list {
  display: flex;
  margin: 0;
  justify-content: flex-end;
  align-items: center;
  width: 100%;
  height: auto;
  text-align: center;
  top: 5.625rem;
  z-index: 999;
  margin: 0 auto;
}
@media (max-width: 63.9375em) {
  .nav__list {
    display: none;
  }
}
.nav__list--open {
  position: absolute;
  display: block;
  background-color: white;
  width: calc(100% - 3rem);
  left: 50%;
  transform: translate(-50%);
  height: auto;
  text-align: center;
  top: 5.625rem;
  padding: 1.25rem 0 2.5rem 0;
  z-index: 999;
  margin: 0 auto;
  border-radius: 0 0 0.3125rem 0.3125rem;
}
@media (max-width: 63.9375em) {
  .nav__list--open {
    top: 3.75rem;
  }
}
@media (max-width: 39.9375em) {
  .nav__list--open {
    top: 4.5rem;
  }
}

.frontPage__hero {
  background-image: url(/img/index-handsIn_1920x1080.jpg);
  background-repeat: no-repeat;
  background-position: center top;
  background-size: cover;
  left: 49%;
  transform: translateX(-49%);
  position: relative;
  margin: 0 0 5rem 0;
  height: 100vh;
  overflow: hidden;
  clear: both;
}
@media (max-width: 63.9375em) {
  .frontPage__hero {
    background-position: 75% top;
    background-color: black;
  }
}
.frontPage__hero--headline {
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  position: absolute;
  top: 40%;
  left: 20%;
  z-index: 900;
  width: 100%;
}
@media (max-width: 63.9375em) {
  .frontPage__hero--headline {
    left: 10%;
    right: 5%;
    width: 80%;
  }
}
@media (max-width: 39.9375em) {
  .frontPage__hero--headline {
    top: 25%;
    text-align: center;
    left: 5%;
    width: 90%;
  }
}
.frontPage__hero--headline h1,
.frontPage__hero--headline h2 {
  font-weight: 700;
  color: var(--white);
  text-shadow: 0 0 0.4375rem var(--black);
}
.frontPage__hero--headline h1 {
  font-size: 2.5rem;
  margin-bottom: 1.875rem;
  width: 40%;
}
@media (max-width: 63.9375em) {
  .frontPage__hero--headline h1 {
    width: 100%;
  }
}
@media (max-width: 63.9375em) {
  .frontPage__hero--headline h1 {
    font-size: 2rem;
  }
}
.frontPage__hero--headline h2 {
  font-size: 1.75rem;
  margin-bottom: 1.5625rem;
  width: 30%;
}
@media (max-width: 63.9375em) {
  .frontPage__hero--headline h2 {
    width: 80%;
  }
}
@media (max-width: 39.9375em) {
  .frontPage__hero--headline h2 {
    font-size: 1.5rem;
    line-height: 1.3;
    width: 100%;
  }
}

.full__width--about {
  background-color: var(--corpRed);
  margin-bottom: 6.25rem;
}
.full__width--recruiting {
  margin-bottom: 6.25rem;
}
.full__width--connect {
  background-image: url("/img/lets_connect_bkgd_1920x300.jpg");
  background-repeat: no-repeat;
  background-position: center center;
  width: cover;
  height: 18.75rem;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 6.25rem;
}
.full__width--testimonials {
  background-image: url("/img/index_testimonials_1920x350.jpg");
  background-repeat: no-repeat;
  background-position: center center;
  width: cover;
  height: 15.625rem;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  margin-bottom: 2.5rem;
}

.frontpg__about {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 0;
  position: relative;
  width: 100%;
  height: 18.75rem;
}
@media (max-width: 63.9375em) {
  .frontpg__about {
    height: auto;
  }
}
@media (max-width: 39.9375em) {
  .frontpg__about {
    flex-direction: column;
    height: auto;
  }
}
.frontpg__about--img, .frontpg__about--text {
  width: 50%;
}
@media (max-width: 39.9375em) {
  .frontpg__about--img, .frontpg__about--text {
    width: 100%;
    margin-bottom: 1.25rem;
    overflow: hidden;
  }
}
.frontpg__about--img {
  flex: 1;
  height: 18.75rem;
  overflow: hidden;
}
@media (max-width: 63.9375em) {
  .frontpg__about--img {
    height: auto;
  }
}
.frontpg__about--img img {
  width: 100%;
  display: block;
}
.frontpg__about--text {
  color: var(--white);
  font-weight: 500;
  padding: 0 1.25rem;
  width: 50%;
}
@media (max-width: 39.9375em) {
  .frontpg__about--text {
    padding: 0 0.9375rem;
    width: 100%;
  }
}
.frontpg__about--text h2 {
  font-size: 2.25rem;
  margin-bottom: 0.5rem;
}
@media (max-width: 39.9375em) {
  .frontpg__about--text h2 {
    font-size: 24;
  }
}
.frontpg__about--text p {
  font-size: 1.25rem;
  line-height: 1;
}
@media (max-width: 39.9375em) {
  .frontpg__about--text p {
    font-size: 1.125rem;
  }
}
.frontpg__recruiting {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 0;
  position: relative;
  width: 100%;
}
@media (max-width: 63.9375em) {
  .frontpg__recruiting {
    height: auto;
  }
}
@media (max-width: 39.9375em) {
  .frontpg__recruiting {
    flex-direction: column;
    height: auto;
  }
}
.frontpg__recruiting--img {
  flex: 1;
  overflow: hidden;
  box-shadow: 0 0 0.625rem var(--black);
}
@media (max-width: 63.9375em) {
  .frontpg__recruiting--img {
    height: auto;
  }
}
@media (max-width: 39.9375em) {
  .frontpg__recruiting--img {
    order: 1;
    margin-bottom: 1.25rem;
  }
}
.frontpg__recruiting--img img {
  width: 100%;
  overflow: hidden;
  display: block;
}
.frontpg__recruiting--text {
  font-weight: 500;
  padding: 0 1.25rem;
  width: 44%;
}
@media (max-width: 39.9375em) {
  .frontpg__recruiting--text {
    padding: 0 0.9375rem;
    width: 100%;
    order: 2;
  }
}
.frontpg__recruiting--text h2 {
  color: var(--corpRed);
  font-size: 2.25rem;
  margin-bottom: 0.5rem;
}
@media (max-width: 39.9375em) {
  .frontpg__recruiting--text h2 {
    font-size: 28;
  }
}
.frontpg__recruiting--text p {
  color: var(--textGrey);
  font-size: 1.375rem;
  line-height: 1.3;
  width: 80%;
}
@media (max-width: 39.9375em) {
  .frontpg__recruiting--text p {
    font-size: 1.125rem;
  }
}
.frontpg__testimonials {
  display: flex;
  justify-content: flex-start;
  align-items: flex-end;
  padding: 0 0 1.25rem 1.25rem;
}
.frontpg__testimonials h3 {
  font-family: var(--font-heading);
  font-size: 3rem;
  font-weight: 700;
  letter-spacing: 0.125rem;
  color: var(--white);
  text-transform: uppercase;
  text-shadow: 0.1875rem 0.1875rem 0.3125rem var(--black);
}
@media (max-width: 39.9375em) {
  .frontpg__testimonials h3 {
    font-size: 2.25rem;
  }
}
.frontpg__testimonials--section {
  margin-bottom: 5rem;
}
.frontpg__testimonials--testimonial {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1.25rem;
}
.frontpg__testimonials--testimonialSingle {
  padding: 1.875rem;
}
@media (max-width: 63.9375em) {
  .frontpg__testimonials--testimonialSingle {
    padding: 0 0.625rem 1.875rem;
  }
}
.frontpg__testimonials--testimonialSingle p {
  font-family: var(--font-text);
  font-size: 1.125rem;
  color: var(--textGrey);
  font-weight: 500;
  line-height: 1.3;
}
.frontpg__testimonials--testimonialSingle a {
  color: var(--corpRed);
}
.frontpg__testimonials--testimonialSingle a:hover {
  color: var(--darkGrey);
  text-decoration: underline;
}
@media (max-width: 39.9375em) {
  .frontpg__testimonials--testimonial {
    flex-wrap: wrap;
  }
}

.page__connect {
  background-color: var(--corpRed80);
  height: 12.5rem;
  border: 0.625rem solid var(--white);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.page__connect h2 {
  font-size: 2.25rem;
  color: var(--white);
  text-transform: uppercase;
  font-weight: 500;
}

.index__input {
  padding: 0.625rem;
}

input::placeholder {
  font-family: var(--font-heading);
  font-size: 1.625rem;
  color: var(--midGrey);
  font-weight: 500;
  padding: 0.625rem;
}

.testimonial {
  border: 0 0.0625rem solid var(--midGrey);
  border-top: 0.625rem solid var(--corpRed);
  padding: 1.25rem;
}
.testimonial__logo {
  margin-bottom: 0.9375rem;
}
.testimonial:hover {
  box-shadow: 0 0 1.25rem var(--corpRed);
  border: 0;
  border-top: 0.625rem solid var(--corpRed);
  transition: all 300ms ease-in-out;
}

.testimonials__all {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding-right: 1.875rem;
}
@media (max-width: 63.9375em) {
  .testimonials__all {
    padding-right: 1.25rem;
  }
}
.testimonials__all p a {
  font-family: var(--font-text);
  font-size: 0.875rem;
  color: var(--corpRed);
  text-transform: uppercase;
  cursor: pointer;
}
.testimonials__all p a:hover {
  text-decoration: underline;
  color: var(--linkHover);
}

/* Footer
/* -------------------------------------*/
.footer {
  height: auto;
  background-color: var(--lightGrey);
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 1.875rem 0;
}
.footer__identity, .footer__menu {
  display: flex;
  justify-content: center;
  align-items: center;
}
.footer__nav {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}
.footer__nav--item {
  margin: 0 0 0 1.5rem;
}